.cats-table {
  display: flex;
  flex-wrap: wrap;
}

.row {
  display: flex;
}

.column-pic, .column-pink, .column-beige, .column-beige-flipped {
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 50%;
}

.column-pic {
  min-width: 50%;
}

.column-pink {
  background-color: #FFDBDB;
  padding: 5vw;
}

.column-beige {
  background-Color: #EECE96;
  padding: 5vw;

}

.column-beige-flipped {
  background-Color: #EECE96;
  padding: 5vw;

}

@media (max-width: 768px) {
  .row {
    flex-direction: column; /* Change flex direction to column for stacking */
  }

  .column-pic {
    width: 100%; 
    height: 40vh;
  }

  .column-pink, .column-beige, .column-beige-flipped {
    width: 100%;
    padding-bottom: 10vh;
    box-sizing: border-box;
  }

  .column-beige-flipped:nth-child(1) {
    order: 2; /* Set the order of first column to 2 */
  }

  .flipped:nth-child(2) {
    order: 1; /* Set the order of second column to 1 */
  }
  
}
