.hero {
    grid-area: hero;
    height: 91vh;
    display: flex;
    position: relative;
    overflow: hidden;
    background-color: rgb(94, 43, 43);
  }
  
  .hero-img {
    width: 100%;
    object-fit: cover;
    opacity: 0.4;
  }
  
  .hero-text-container {
    position: absolute;
    top: 50%;
    left: 50%; 
    transform: translate(-50%, -50%);
    text-align: center; 
    width: 100%;
  }
  
  .hero-text1 {
    color: rgb(255, 255, 255);
    font-size: calc(60px + 5vw);
    text-shadow: 0px 0px 20px hsla(0, 83%, 12%, 0.8);
    font-family: 'League Script';
    -webkit-text-stroke: 0.1vw rgb(255, 255, 255);

  }
  
  .hero-text2 {
    color: white;
    font-size: calc(26px + 1vw);
    text-shadow: -2px 2px 10px hsla(0, 83%, 12%, 0.5);
    font-family: 'Gruppo';
    letter-spacing: 1.5vw;
  }

  @media (max-width: 505px) {

    .hero-text1 {
        font-size: calc(10px + 15vw);
      }
      
      .hero-text2 {
        font-size: calc(6px + 4vw);
      }
  }