.header {
  grid-area: header;
  height: 50vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header-img {
  width: 100%;
  object-fit: cover;
  opacity: 0.75;
}

.header-text {
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -80%); /* Center the text */
  color: white; /* Change text color if necessary */
  font-size: calc(32px + 3vw);
  text-shadow: 3px 3px 5px hsla(0, 83%, 12%, 0.5);
  font-family: 'Karla';
  line-height: 1.25;
}

@media (max-width: 768px) {
  .header-text {
    font-size: calc(22px + 2vh);
  }

}