.footer {
    grid-area: footer;
    font-family: Bungee;
    display: flex;
    padding-left: 5px;
    flex-wrap: wrap; /* Allow items to wrap if they exceed container width */
    justify-content: center;
    background-color: #EECE96;
    color: #601C0B;
    column-gap: 10%;
    padding-top: 5%;
    padding-bottom: 1%;
    font-size: 1.15rem;
    line-height: 1.7rem;
  }
  
  .footer-column {
    display: flex; /* Make children display in a row */
    flex-direction: column; /* Stack items vertically inside column */
    align-items: flex-start; /* Align items to the left */
  }
  
  .footer-column p {
    margin: 5px; /* Change default paragraph margins */
  }
  
  .footer-logo {
    width: 100%; /* Ensure the logo spans the full width */
    display: flex;
    justify-content: center; /* Center the logo horizontally */
    margin-top: 2%; /* Add space between columns and logo */
  }

  .footer-logo img {
    height: 85%; 
  }

  .some-icon {
    height: 50px;
    padding-top: 0.7vh;
    padding-right: 15px;
  }

  @media (max-width: 650px) {
    .footer {
      font-size: 0.70rem;
      column-gap: 1%;
      padding-left: 0px;
      padding-right: 0px;
      padding-top: 8%;
    }
    .footer-logo {
      margin-top: 5%; /* Add space between columns and logo */
      margin-bottom: 2%;
    }
    .footer-logo img {
      height: 100%; /* Ensure the logo spans the full width */
    }
    .some-icon {
      height: calc(20px + 2vw);
      padding-right: 10px;
    }
  }

  @media (min-width: 2000px) {
    .footer {
      padding-top: 3%;
      font-size: 1.25rem;
      column-gap: 20%;
    }
  }
  