.navbar {
  grid-area: navigationbar;
  background-color: #eece96;
  color: #601c0b;
  font-family: Bungee;
  font-size: 2vw;
  padding: 0 2vw;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  padding-top: 1vh;
  padding-bottom: 0.5vh;
}

.logo {
  max-width: 40vw;
  height: 8vh;
}

.nav-links {
  display: flex;
  gap: 3vw;
}

.nav-link {
  white-space: nowrap;
  text-decoration: none;
}

.navbar .nav-link,
.nav-link:visited {
  color: #601c0b;
}

.navbar .nav-link:hover,
.nav-link.active,
.nav-link:active  {
  color: #D16366;
}
@media (max-width: 768px) {
  .navbar {
    font-size: 4vw;
  }

  .logo {
    max-width: 25vw;
  }
}

@media (min-width: 1500px) {
  .navbar {
    font-size: 1.5vw;
  }
}