.content {
  grid-area: content;
  background-color: #ffefef;
  color: #601c0b;
  display: flexbox;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: Karla;
  font-size: 1.2rem;
  line-height: 1.8rem;
}

h3 {
  font-size: 1.8rem;
  line-height: 2rem;
}

h4 {
  font-size: 1.4rem;
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .content {
    font-size: 0.9rem;
  }

  h3 {
    font-size: 1.5rem;
  }

  h4 {
    font-size: 1rem;
  }
}

@media (min-width: 1500px) {
  .content {
    margin-left: 10%;
    margin-right: 10%;
  }
}

@media (min-width: 2000px) {
  .content {
    margin-left: 20%;
    margin-right: 20%;
  }
}
