.form {
  padding: 10%;
  background-color: #EECE96;
}

.textarea {
  width: 90%;
  margin-top: 0.5%;
  margin-bottom: 3%;
  padding: 10px 10px;
  font-family: Karla;
  font-size: 1rem;
  color: #601c0b;
  border: 2px solid #601c0b;
  border-radius: 5px;
}

.large-textarea {
  height: 15vh;
  width: 90%;
  margin-top: 0.5%;
  margin-bottom: 3%;
  padding: 10px 10px;
  font-family: Karla;
  font-size: 1rem;
  color: #601c0b;
  resize: vertical;
  border: 2px solid #601c0b;
  border-radius: 5px;
}

.submit-button {
    font-family: Karla;
    letter-spacing: 2px;
    background-color: #601C0B;
    color:  #ffffff;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 1%;
    margin-bottom: 5%;
    font-size: 1.2rem;
  }
  
  .submit-button:hover {
    background-color: #D16366;
  }


