.contactpage-container {
  display: flex;
}

.contactpage-form-container,
.contactpage-text-container {
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 50%;
}

.contactpage-form-container {
  min-width: 50%;
}
.contactpage-text-container {
  font-size: 1.3rem;
  background-color: #ffdbdb;
  padding: 5vw;
}

@media (max-width: 768px) {
  .contactpage-container {
    flex-direction: column; 
  }

  .contactpage-form-container {
    width: 100%;
  }

  .contactpage-text-container {
    width: 100%;
    box-sizing: border-box;
  }
}
