.frontpage-container {
    display: flex;
    align-items:center;
    justify-content: center;
    margin: 10%;
}

.frontpage-text-container {
    font-size: 1.3rem;
    width: 50%;
    margin-right: 15%;
  }
  
  .frontpage-image-container {
    width: 50%;
  }
  
  .frontpage-image-container img {
    width: 100%;
    border-radius: 10px;
  }
  
  @media (max-width: 768px) {
  
    .frontpage-container {
       flex-direction: column;
    }

    .frontpage-text-container {
        width: 85%;
        margin-top: 5%;
        margin-right: 0;
        padding: 0;
    }
  
    .frontpage-image-container {
        width: 85%;
        margin: 5%;
        padding: 0;
    }
  }