.menu-categories-container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  margin-right: 2%;
  margin-left: 2%;
  gap: 2%;
}

.menu-category {
  background-color: #fff9f9;
  flex: 1 40%;
  margin-bottom: 2%;
  padding: 2%;
  padding-bottom: 5%;
}

.menu-category p {
    margin: 0px;
  }

@media (max-width: 768px) {
  .menu-categories-container {
    display: flex;
    flex-wrap: wrap;
    margin: 4%;
  }

  .menu-category {
    flex: 1 90%;
    padding: 5%;
    padding-bottom: 10%;
    margin: 2%;
  }
}

@media (min-width: 1500px) {
    .menu-categories-container {
        display: flex;
        flex-wrap: wrap;
        margin-top: 2%;
        margin-right: 0%;
        margin-left: 0%;
        gap: 2%;
      }
      
      .menu-category {
        background-color: #fff9f9;
        flex: 1 40%;
        margin-bottom: 2%;
        padding: 3%;
        padding-bottom: 5%;
      }
}
  